import React, { useEffect, useState } from "react";
import "./style.css";
import {Link} from "react-router-dom"
const Navbar = ({sendSearch}) => {
    const [search, setSearch] = useState("");
    const cartItems = JSON.parse(localStorage.getItem("products")) === null ? [] : JSON.parse(localStorage.getItem("products"));
    let cartCount = JSON.parse(localStorage.getItem("cart-count")) === null ? 0 : JSON.parse(localStorage.getItem("cart-count"));
    useEffect(() => {
        sendSearch(search);
    },[search])
    return (
        <header>
            <span id="nav-brand"><b>SHOP</b>LANE</span>
            <nav>
                <Link to="/">CLOTHING</Link>
                <Link to="/">ACCESSORIES</Link>
                <div id="search-wrapper">
                    <input onChange={e => setSearch(e.target.value)} id="search" type="text" value={search} placeholder="Search for Clothing and Accessories" />
                    <i className="fas fa-search"></i>
                </div>
                <Link to="/checkout">
                    <div id="cart">
                        <i className="fas fa-shopping-cart"></i>
                        {cartCount > 0 ? <div style={{display: "flex"}} id="cart-count">{cartCount}</div> :<div  id="cart-count">{cartCount}</div>}
                    </div>
                </Link>
                <i className="fa fa-user-circle"></i>
            </nav>
            <div id="toggle-button">
                <i className="fas fa-bars"></i>
            </div>
        </header>)
}
export default Navbar;